import "./consiglio.js";
import { StringRenderer } from "../../commons/js/string_renderer.js";
import { ServizioComunicatiStampa, VistaComunicatiStampa } from "./comunicati-stampa.js";

$(document).ready(function () {

    function renderDatiOrganismo() {
        let urlDatiOrganismo = apibase.baseurl + "/oi/10400/" + idOrganismo;
        $.ajax({
            url: urlDatiOrganismo, success: function (result) {
                if (result) {
                    $('*[data-title]').each(function () {
                        $(this).html(result.title);
                    });
                    if (result.properties.descrizione) {
                        $('#descrizioneOrganismo').html(result.properties.descrizione);
                    }
                    if (result.components) {
                        // const placeholder = "https://placeimg.com/760/280/any";
                        const placeholder = "https://via.placeholder.com/760x280/006BAC/ffffff?text=" + result.title;
                        let bannerSrc = placeholder;
                        if (result.components.banner && result.components.banner.components && result.components.banner.components.length) {
                            const banner = result.components.banner;
                            bannerSrc = baseImgUrl + banner.components[0].properties.address;
                        }
                        $('#colonnaBanner').html('<img width="760" height="280" src="' + bannerSrc + '" class="img-fluid" >');
                        renderLinkUtili(result);
                    }
                }
            }
        });
    }

    function renderLinkUtili(result) {
        if (result.components.link_utili
            && result.components.link_utili.components
            && result.components.link_utili.components.length) {
            const componenteLink = [[], [], []];
            const linkUtili = result.components.link_utili.components;
            const itemPerColonna = Math.ceil(linkUtili.length / componenteLink.length);
            linkUtili.forEach(function (link, index) {
                let colonna = Math.trunc(index / itemPerColonna);
                componenteLink[colonna].push('<div><a class="link-utile p-2 h6" href="' + link.properties.url + '">' + link.properties.title + '</a></div>');
            });
            let htmlLink = '';
            for (let i = 0; i < componenteLink.length; i++) {
                htmlLink += '<div class="col-xs-12 col-md-4">' + componenteLink[i].join('') + '</div>';
            }
            $('#linkUtili').html(htmlLink);
        } else {
            $('#linkUtili').hide();
        }
    }

    function renderInEvidenza() {
        var urlInEvidenza = apibase.baseurl + 'oi/10400/' + idOrganismo + '/10403?size=6';
        $('#archivioInEvidenza').html(
            '<a href="archivio-organismo.html?tipo=10403&organismo=' + idOrganismo + '" class="light-obj-name-bold h6 pt-2 pr-2">ARCHIVIO</span>'
        );
        $.ajax({
            url: urlInEvidenza, success: function (result) {
                if (result && result.content && result.content.length) {
                    let htmlInEvidenza = '';
                    result.content.forEach(function (news) {
                        let immagine = {};
                        var address = "";
                        var htmlImg = "";
                        var colNews = "col-12";
                        if (news.components && news.components.immagine) {
                            if (news.components.immagine.components.length > 0) {
                                immagine = news.components.immagine.components[0];
                                address = immagine.components.file.components[0].properties.address;
                                htmlImg = '<img class="img_elenco" src="' + baseImgUrl + address + '" />';
                            }
                        }
                        var linkNews = news.properties.url;
                        var target = '_blank';
                        if (!linkNews) {
                            linkNews = 'post-organismo.html?tipo=10403&id=' + news.id + '&organismo=' + idOrganismo;
                            target = '_self';
                        }
                        var stringedDate = "";
                        if (news.properties.data_pubblicazione != undefined) {
                            stringedDate = StringRenderer.renderDataTestuale(news.properties.data_pubblicazione)
                        } else if (news.lastModifiedDate) {
                            stringedDate = StringRenderer.renderDataTestuale(news.lastModifiedDate)
                        }
                        htmlInEvidenza += '\
                        <div class="item row  mt-0 mr-3 ml-3">\
                            <div class=medium-font-size "' + colNews + '">\
                                <div class="">\
                                    <div class="text-center">' + htmlImg + ' </div>\
                                    <hr class="border-news pr-2 pl-2"/>\
                                    <p class="text-font mt-2 mb-0">' + stringedDate + '</p>\
                                </div>\
                                <h6 >' + news.title + '</h6>\
                                <p class="text-font">' + news.properties.intestazione + '</p>\
                                <a class="read-more" target="' + target + '" href="' + linkNews + '">\
                                    <span class="text">Leggi di pi√π</span>\
                                    <svg class="icon"><use xlink:href="svg/sprite.svg#it-arrow-right"></use></svg>\
                                </a>\
                            </div>\
                        </div>';
                    });
                    $('#inEvidenza').html('<div id="eventsSlider" class="owl-carousel">' + htmlInEvidenza + '</div>');
                    var owl = $("#eventsSlider");
                    owl.owlCarousel({
                        responsiveClass: true,
                        responsive: {
                            0: {
                                items: 1,
                                navigation: true,
                                loop: false
                            },
                            992: {
                                items: 3,
                                navigation: true,
                                loop: false
                            }
                        },
                        navigation: true
                    });
                } else {
                    $('#inEvidenza').html('Non ci sono elementi in evidenza');
                }
            }
        });
    }

    function renderMenu() {
        var urlMenu = apibase.baseurl + 'oi/10400/' + idOrganismo + '/menu';
        $.ajax({
            url: urlMenu,
            success: function (result) {
                if (result && result.length) {
                    let linkFasciaUno = result;
                    var fasciaMenu = '<div class="row justify-content-center">\n';
                    linkFasciaUno.forEach(function (element) {
                        const icon = element.properties.icon;
                        let iconLink = '';//'svg/ba-sprite.svg#ba-bilancio';
                        if (icon) {
                            if (element.properties.icon.indexOf("it-") != -1) {
                                iconLink = "svg/sprite.svg#" + element.properties.icon;
                            }
                            if (element.properties.icon.indexOf("ba-") != -1) {
                                iconLink = "svg/ba-sprite.svg#" + element.properties.icon;
                            }
                        }
                        const url = 'sottopagina-organismo.html?organismo=' + idOrganismo + '&sezione=' + element.id;
                        const box = '\
                            <div class="box-menu col-6 col-sm-4 col-md-3 text-center align-content-center">\
                                <a href="' + url + '" class="text-fascia-2">\
                                    <div class ="box-fascia-2 text-center">\
                                        <svg class="icon-fascia-2"><use xlink:href="' + iconLink + '"></use></svg>\
                                    </div>\
                                    <p class="mt-2">' + element.properties.title.toUpperCase() + ' </p>\
                                </a>\
                           </div>';
                        fasciaMenu += box;
                    });
                    fasciaMenu += "</div>";
                    $("#fasciaMenu").html(fasciaMenu);
                }
            }
        });
    }


    function renderNews(idOrganismo) {
        var urlNew = apibase.baseurl + 'oi/10400/' + idOrganismo + '/10402?size=2';
        $.ajax({
            url: urlNew, success: function (result) {
                if (result != null && result.content.length > 0) {
                    var news_slider_html = "";
                    var allNews = result.content;
                    allNews.forEach(function (news) {
                        news_slider_html += "";
                        var linkNews = news.properties.url;
                        var target = '_blank';
                        if (!linkNews) {
                            linkNews = 'post-organismo.html?tipo=10402&id=' + news.id + '&organismo=' + idOrganismo;
                            target = '_self';
                        }
                        var stringedDate = StringRenderer.renderDataTestuale(news.properties.data_pubblicazione)
                            + ', ' + news.properties.data_pubblicazione_time.slice(0, 5);
                        news_slider_html += '\
                        <div class="bg-white p-1 mt-2">\
                            <p class="mt-2 ml-2 mb-0 ">\
                                <svg class="icon icon-dark"> <use xlink:href="svg/ba-sprite.svg#ba-news"></use></svg>\
                                <span class="data ml-1">' + stringedDate + '</span>\
                            </p>\
                            <div class="h6 ml-3">\
                                <a class="dark-obj-name-bold" target="' + target + '" href="' + linkNews + '" >' + news.properties.title + '</a>\
                            </div>\
                         </div>';
                    });
                    if (news_slider_html != "") {
                        $("#colonnaNotizie").html(news_slider_html);
                    }
                }
            }
        });
    }

    function renderComunicatiStampa() {
        ServizioComunicatiStampa.carica({ organismo: idOrganismo }).then(function (result) {
            if (result) {
                $('#comunicatiStampa').html(VistaComunicatiStampa.render(result, 'organismo=' + idOrganismo));
            }
        });
    }

    var urlParams = new URLSearchParams(window.location.search);
    const idOrganismo = urlParams.get('id');
    renderDatiOrganismo();
    renderMenu();
    renderInEvidenza();
    renderComunicatiStampa();
    $('#archivioNotizie').html(
        '<a href="archivio-organismo.html?tipo=10402&organismo=' + idOrganismo + '" class="light-obj-name-bold h6 pt-2 pr-2">ARCHIVIO</span>'
    );
    renderNews(idOrganismo);
    window.setInterval(() => {
        renderNews(idOrganismo);
    }, 120000);

});

